
import { defineComponent, ref } from "vue";
import ApiService, { baseUrl } from "@/core/services/ApiService";
import { hideModal } from "@/core/helpers/dom";
import Swal from "sweetalert2";
interface NewProduct {
  title: "",
  description: "",
  category: "",
  type: "Medical",
  trade_price: number,
  mrp: number,
  code: "",
  point: number,
  imagelist: string[],
  customfunds: object[],
  specifications: object[],
  error: string,
}
export default defineComponent({
  name: "main-dashboard",
  data() {
    return {
      members: "",
      preview: "",
      baseUrl: baseUrl,
    };
  },
  mounted() {
    this.getMembers();
  },
  methods: {
    getMembers() {
      ApiService.get("members?type=AGENT").then((response) => {
        console.log(response.data);
        this.members = response.data;
      });
    },
    delMember(id) {
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "Yes, delete it!",
        cancelButtonText: "No, cancel!",
        reverseButtons: true,
      }).then((result) => {
        if (result.isConfirmed) {
          ApiService.delete("members?id=" + id).then((response) => {
            document.getElementById("id-" + id)!.classList.add("animate__lightSpeedOutRight");
            setTimeout(() => {
              Swal.fire("Deleted!", "Your file has been deleted.", "success");
              document.getElementById("id-" + id)!.remove();
            }, 500);
          });
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          Swal.fire("Cancelled", "Your imaginary file is safe :)", "error");
        }
      });
    },
  },
});
